import { ActionContext } from 'vuex';
import { RootState } from '.';

/**
 * Root actions instance.
 *
 * @export
 * @class RootActions
 */
export default class RootActions {
    /**
     * Creates an instance of Action.
     * @param {RootActionContext} _context
     * @memberof Action
     */
    constructor(private _context: ActionContext<RootState, RootState>) {}

    /**
     * Initialize.
     *
     * @export
     * @returns {Promise<void>}
     */
    public initialize(): void {
        this._context.commit('graphql/newSession');
    }

    public initialiseStore(): void {
        try {
            const storage = localStorage.getItem('unicorn-playground');
            if (storage) {
                this._context.state = Object.assign(this._context.state, JSON.parse(storage));
                this._context.dispatch(
                    'graphql/switchSession',
                    (this._context.state as any).graphql.activeSession.id
                );
            }
        } catch (err) {
            console.warn(err);
        }
        if (!(this._context.state as any).graphql.activeSession) {
            this._context.dispatch('graphql/newSession');
        }
    }
}
