import { Component } from 'vue';
import { RootState } from '.';

/**
 * Root mutations instance.
 *
 * @export
 * @class RootMutations
 */
export default class RootMutations {
    /**
     * Creates an instance of Action.
     * @param {RootActionContext} _context
     * @memberof Action
     */
    constructor(private _state: RootState) {}

    /**
     * Set error.
     *
     * @param {Error} error
     * @memberof RootMutations
     */
    public setError(error: Error): void {
        this._state.error = error;
    }

    public setIsInitialized(isInitialized: boolean): void {
        this._state.isInitialized = isInitialized;
    }

    /**
     * Set is use public account.
     *
     * @param {boolean} value
     * @memberof LoginMutations
     */

    public activeWindow(window: Component | null): void {
        this._state.activeWindow = window;
    }

    public setThemeColor(color: string): void {
        this._state.themeColor = color;
    }
}
