import { Module } from 'vuex';
import { RootState } from '@/store';
import { bindActions, bindMutations } from '@/store/helper';
import { END_POINT } from '@/main';
import Mutations from './mutations';
import Actions from './actions';

export interface GraphQlSession {
    title: string;
    id: string;
    query: string;
    variables: string;
    template: string;
    result: string;
    variablesToType: string;
    isBusy: boolean;
    viewCode: boolean;
}

export interface PanelSize {
    min: number;
    max: number;
    size: number;
}

export interface GraphQlConfiguration {
    customEndpoint: string;
    useCustomEndpoint: boolean;
    force: boolean;
    strict: boolean;
    serial: boolean;
    paging: number;
    concurrent: number;
    splitPanelRootSizes: PanelSize[];
    splitPanelChildSizes: PanelSize[];
}

export interface GraphQlState {
    sessions: GraphQlSession[];
    activeSession: GraphQlSession | null;
    configuration: GraphQlConfiguration;
}

export default {
    namespaced: true,
    state: {
        sessions: [],
        activeSession: null,
        isBusy: false,
        configuration: {
            force: false,
            customEndpoint: END_POINT,
            useCustomEndpoint: false,
            concurrent: 0,
            paging: 0,
            serial: false,
            strict: false,
            splitPanelRootSizes: [
                {
                    max: 100,
                    min: 0,
                    size: 50,
                },
                {
                    max: 100,
                    min: 0,
                    size: 0,
                },
                {
                    max: 100,
                    min: 0,
                    size: 50,
                },
            ],
            splitPanelChildSizes: [
                {
                    max: 100,
                    min: 0,
                    size: 75,
                },
                {
                    max: 100,
                    min: 0,
                    size: 25,
                },
            ],
        },
    },
    mutations: bindMutations(Mutations, {
        setQuery: (_) => _.setQuery,
        setVariables: (_) => _.setVariables,
        setVariableToType: (_) => _.setVariableToType,
        setTemplate: (_) => _.setTemplate,
        setResult: (_) => _.setResult,
        setIsBusy: (_) => _.setIsBusy,
        setActiveSession: (_) => _.setActiveSession,
        updateActiveSessionInfo: (_) => _.updateActiveSessionInfo,
        addSession: (_) => _.addSession,
        removeSession: (_) => _.removeSession,
        updateConfiguration: (_) => _.updateConfiguration,
    }),
    actions: bindActions(Actions, {
        runQuery: (_) => _.runQuery,
        stopQuery: (_) => _.stopQuery,
        prettifyQuery: (_) => _.prettifyQuery,
        mergeQuery: (_) => _.mergeQuery,
        newSession: (_) => _.newSession,
        switchSession: (_) => _.switchSession,
        closeSession: (_) => _.closeSession,
    }),
} as Module<GraphQlState, RootState>;
