import CodeMirror from 'codemirror';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
CodeMirror.registerHelper('hint', 'liquid', (editor, options) => {
    const cur = editor.getCursor();
    const token = editor.getTokenAt(cur);
    // const rawResults = getAutocompleteSuggestions(schema, editor.getValue(), token);
    const tokenStart =
        token.type !== null && /"|\w/.test(token.string[0]) ? token.start : token.end;
    const results = {
        list: [
            {
                text: '{%- assign   -%}',
                // isDeprecated: false,
                // deprecationReason: null,
            },
            {
                text: '{%- if   -%}\n{%- endif -%}',
            },
            {
                text: '{%- for   -%}\n{%- endfor -%}',
            },
            {
                text: '{%- capture temp -%}\n{%- endcapture -%}',
            },
            {
                text: '{%- unless forloop.last -%},{%- endunless -%}',
            },
        ],
        from: {
            line: cur.line,
            column: tokenStart,
        },
        to: {
            line: cur.line,
            column: token.end,
        },
    };

    if (results && results.list && results.list.length > 0) {
        results.from = CodeMirror.Pos(results.from.line, results.from.column);
        results.to = CodeMirror.Pos(results.to.line, results.to.column);
        CodeMirror.signal(editor, 'hasCompletion', editor, results, token);
    }

    return results;
});
