







































































































































import Vue from 'vue';
import AppTile from '@/components/layout/Tile.vue';
import moment from 'moment';
import { GraphQlState } from '@/store/modules/graphql';

export default Vue.extend({
    name: 'app-bar',
    components: {
        AppTile,
    },
    props: {
        icon: {
            type: String,
            required: false,
        },
        title: {
            type: String,
            required: false,
        },
        prominent: {
            type: Boolean,
            required: false,
            default: false,
        },
        flat: {
            type: Boolean,
            required: false,
            default: true,
        },
        clipped: {
            type: Boolean,
            required: false,
            default: false,
        },
        drawer: {
            type: Boolean,
            required: false,
            default: true,
        },
        app: {
            type: Boolean,
            required: false,
            default: true,
        },
    },
    computed: {
        routeName(): string {
            return this.$route.name || '';
        },
        date(): string {
            return moment(this.now).format('M/DD/YYYY');
        },
        time(): string {
            return moment(this.now).format('h:mm A');
        },
        themeColor(): string {
            return this.$store.state.themeColor;
        },
        useCustomEndpoint(): boolean {
            return (this.$store.state.graphql as GraphQlState).configuration.useCustomEndpoint;
        },
        customEndpoint(): string {
            return (this.$store.state.graphql as GraphQlState).configuration.customEndpoint;
        },
    },
    methods: {
        handleChangeUseCustomEndpoint(useCustomEndpoint: boolean): void {
            this.$store.commit('graphql/updateConfiguration', { useCustomEndpoint });
        },
        handleChangeCustomEndpoint(customEndpoint: string): void {
            this.$store.commit('graphql/updateConfiguration', { customEndpoint });
        },
        goto(path: string): void {
            this.$router.push(path || '/');
        },
        handleChangeColor(color: string): void {
            this.$store.commit('setThemeColor', color);
        },
    },
    data() {
        return {
            now: new Date().toISOString(),
        };
    },
    mounted() {
        setInterval(() => {
            this.now = new Date().toISOString();
        }, 1000);
    },
});
