













import Vue from 'vue';

export default Vue.extend({
    name: 'app-bar',
    props: {
        icon: {
            type: String,
            required: true,
        },
        title: {
            type: String,
            required: true,
        },
        active: {
            type: Boolean,
            required: false,
            default: false,
        },
        bottomLine: {
            type: Boolean,
            required: false,
            default: true,
        },
    },
    computed: {
        width(): string {
            return '100%';
        },
        height(): string {
            return '100%';
        },
        styleRoot(): string {
            return `
                width: 52px; 
                height: 48px; 
                display: flex; 
                justify-content: center; 
                align-items: center;
                position: relative;
            `;
        },
    },
    data() {
        return {
            isMousedown: false,
        };
    },
    methods: {
        handleMouseDown(): void {
            this.isMousedown = true;
        },
        handleMouseClick(): void {
            this.$emit('click');
        },
        handleMouseUp(): void {
            this.isMousedown = false;
        },
    },
});
