














import Vue from 'vue';
import { mapState } from 'vuex';

export default Vue.extend({
    props: ['persistent'],
    data() {
        return { currentPath: '' };
    },
    computed: {
        ...mapState(['error']),
        errorTree(): string {
            return this.error.stack?.replace(/\n/, '<br />').replace(/\n/g, '<br />➢ ');
        },
        themeColor(): string {
            return this.$store.state.themeColor;
        },
    },
    beforeMount() {
        this.currentPath = this.$route.path;
    },
});
