import { ActionContext, ActionTree, MutationTree } from 'vuex';

/**
 * Bind actions class to function context.
 *
 * @export
 * @template Actions Action type.
 * @template RootState Root state type.
 * @template ChildState Child state type.
 * @param {new (_: ActionContext<RootState, ChildState>) => Actions} action
 * @param {{ [name: string]: (c: Actions) => (payload?: any) => any }} mapper
 * @returns {ActionTree<RootState, ChildState>}
 */
export function bindActions<Actions, RootState, ChildState>(
    action: new (_: ActionContext<RootState, ChildState>) => Actions,
    mapper: { [name: string]: (c: Actions) => (payload?: any) => any }
): ActionTree<RootState, ChildState> {
    const result: ActionTree<RootState, ChildState> = {};

    for (const item in mapper) {
        if (Object.prototype.hasOwnProperty.call(mapper, item)) {
            // eslint-disable-next-line no-loop-func
            result[item] = (context: ActionContext<RootState, ChildState>, payload?: any): any => {
                // eslint-disable-next-line new-cap
                const instance = new action(context);
                const method = mapper[item](instance);
                return method.call(instance, payload);
            };
        }
    }

    return result;
}

/**
 * Bind mutations to function context
 *
 * @export
 * @template Mutations Mutaltion type.
 * @template State State type.
 * @param {new (_: State) => Mutations} mutation
 * @param {{ [name: string]: (c: Mutations) => (payload?: any) => any }} mapper
 * @returns {MutationTree<State>}
 */
export function bindMutations<Mutations, State>(
    mutation: new (_: State) => Mutations,
    mapper: { [name: string]: (c: Mutations) => (payload?: any) => any }
): MutationTree<State> {
    const result: MutationTree<State> = {};

    for (const item in mapper) {
        if (Object.prototype.hasOwnProperty.call(mapper, item)) {
            // eslint-disable-next-line no-loop-func
            result[item] = (state: State, payload?: any): any => {
                // eslint-disable-next-line new-cap
                const instance = new mutation(state);
                const method = mapper[item](instance);
                return method.call(instance, payload);
            };
        }
    }
    return result;
}
