import Vue from 'vue';
import { VNode } from 'vue/types/umd';
import VueRouter from 'vue-router';
import _ from 'lodash';
import AppMain from './Main.vue';
import store from './store';
import vuetify from './plugins/vuetify';
import './plugins';
import './registerServiceWorker';
import { SET_ERROR, SET_IS_INITIALIZED } from './mutation-types';
import { INITIALIZE } from './action-types';
import router from './router';
import { GraphQlState } from './store/modules/graphql';

export const END_POINT = `${window.location.origin}/graphql`;

// Enable Vue Development tool for Development mode
Vue.config.devtools = process.env.NODE_ENV === 'development';

const debounceSetStorage = _.debounce((state) => {
    if (state.graphql.activeSession) {
        const stateCopy = JSON.parse(JSON.stringify(state));
        stateCopy.error = null;
        stateCopy.graphql.activeSession.result = '';
        stateCopy.graphql.activeSession.isBusy = false;
        stateCopy.graphql.activeSession.viewCode = false;
        (stateCopy.graphql as GraphQlState).sessions.forEach((session) => {
            session.result = '';
            session.isBusy = false;
            session.viewCode = false;
        });

        localStorage.setItem('unicorn-playground', JSON.stringify(stateCopy));
    }
}, 1000);

store.subscribe((_mutation, state) => {
    debounceSetStorage(state);
});

export class Main {
    public static app: Vue;

    public static main(): void {
        Vue.config.errorHandler = (err, vm, info): void => {
            err.stack += `\nat component:
                            ├── Error information: ${info}`;
            console.log(vm);
            if (vm.$options.name) {
                err.stack += `\n├── Component name: ${vm.$options.name}`;
            }
            if (process.env.NODE_ENV === 'development') {
                console.error(err.message);
            }
            store.commit('initialiseStore');
        };
        Main.app = new Vue({
            name: 'Root Component',
            vuetify,
            store,
            router: new VueRouter({
                mode: 'hash',
                base: '/dev/playground/',
                // mode: 'abstract',
            }),
            beforeCreate(): void {
                this.$store.dispatch('initialiseStore');
                this.$store.commit(SET_ERROR, null);
            },
            render: (h): VNode => h(AppMain),
        }).$mount('#app');
    }

    public static async initialize(): Promise<void> {
        // Initialize store.
        await store.dispatch(INITIALIZE);

        // Mapping router
        const routerConfig = router();
        Main.app.$router.addRoutes(routerConfig);
        Main.app.$router.afterEach((to) => {
            document.title = `PLAYGROUND -  ${to.meta?.text}`;
        });
        store.commit(SET_IS_INITIALIZED, true);
    }
}

Main.main();
