import Vue, { Component } from 'vue';
import Vuex from 'vuex';
import { SET_ERROR, SET_IS_INITIALIZED } from '@/mutation-types';
import { INITIALIZE } from '@/action-types';
import RootMutations from './mutations';
import about from './modules/about';
import graphql from './modules/graphql';
import pdf from './modules/pdf';
import RootActions from './actions';
import { bindActions, bindMutations } from './helper';

Vue.use(Vuex);

/**
 * Root Store State.
 *
 * @interface StoreState
 */
export interface RootState {
    error: null | Error;
    isInitialized: boolean;
    activeWindow: Component | null;
    themeColor: string;
}

export default new Vuex.Store<RootState>({
    modules: {
        about,
        graphql,
        pdf,
    },
    state: {
        error: null,
        isInitialized: false,
        activeWindow: null,
        themeColor: '#1a237e',
    },
    mutations: bindMutations(RootMutations, {
        [SET_ERROR]: (_) => _.setError,
        [SET_IS_INITIALIZED]: (_) => _.setIsInitialized,
        setActiveWindow: (_) => _.activeWindow,
        setThemeColor: (_) => _.setThemeColor,
    }),
    actions: bindActions(RootActions, {
        [INITIALIZE]: (_) => _.initialize,
        initialiseStore: (_) => _.initialiseStore,
    }),
});
