import CodeMirror from 'codemirror';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
CodeMirror.registerHelper('hint', 'javascript', (editor: CodeMirror.Editor, options) => {
    const cur = editor.getCursor();
    const token = editor.getTokenAt(cur);
    // const rawResults = getAutocompleteSuggestions(schema, editor.getValue(), token);
    const tokenStart =
        token.type !== null && /"|\w/.test(token.string[0]) ? token.start : token.end;
    const results = {
        list: [
            {
                text: `{ // Text
    text: 'Right part', 
    alignment: 'right',
}`,
            },
            {
                text: `{ // Columns
    columns: [
        { width: '*', text: 'First column' }, 
        { width: 'auto', text: 'Second column' } 
    ], columnGap: 10 
}`,
            },
            {
                text: `{ // Table
    layout: 'lightHorizontalLines',
    table: { headerRows: 1, widths: [ '*', '100'], 
        body: [ [ 'First', 'Second', 'Third', 'The last one' ],[ 'Value 1', 'Value 2', 'Value 3', 'Value 4' ]] 
    }
}`,
            },
            {
                text: `{ // Unorder list
    ul: [
        'First',
        'Second',
        'Third'
    ]
}`,
            },
            {
                text: `{ // Order list
    ol: [
        'First',
        'Second',
        'Third']
}`,
            },
            {
                text: `footer: function(currentPage, pageCount) {  // Page footer
    return currentPage.toString() + ' of ' + pageCount;
}`,
            },
            {
                text: `{ // Image
    image: 'data:image/jpeg;base64,...encodedContent...',
    width: 150
}`,
            },
            {
                text: `{ // QR code
    qr: 'text in QR',
    foreground: 'red',
    background: 'yellow',
    fit: '500',
}`,
            },
        ],
        from: {
            line: cur.line,
            ch: tokenStart,
        },
        to: {
            line: cur.line,
            ch: token.end,
        },
    };

    if (results && results.list && results.list.length > 0) {
        results.from = CodeMirror.Pos(results.from.line, results.from.ch);
        results.to = CodeMirror.Pos(results.to.line, results.to.ch);
        CodeMirror.signal(editor, 'hasCompletion', editor, results, token);
    }

    return results;
});
