import qs from 'qs';
import { END_POINT } from '@/main';

export interface GraphQlRequestOption {
    url?: string;
    paging?: number;
    concurrent?: number;
    force?: boolean;
    template?: string;
    aborter?: AbortController | null;
    strict?: boolean;
    serial?: boolean;
}

const fetchGraphQl = async function (
    query: string,
    options: GraphQlRequestOption = {
        url: END_POINT,
    },
    variables: any = null
): Promise<any | any[]> {
    const { url } = options;
    const { paging, concurrent, force, aborter, strict, serial } = options;
    const getConcurrent = (_c: undefined | number): undefined | number => {
        if (_c === undefined) return undefined;
        if (_c <= 0) return undefined;
        return _c;
    };
    const getPaging = (_p: undefined | number): undefined | number | boolean => {
        if (_p === undefined) return undefined;
        if (_p.toString() === '0') return undefined;
        if (_p < 0) return false;
        return _p;
    };

    const queryString = qs.stringify(
        {
            concurent: getConcurrent(concurrent),
            paging: getPaging(paging),
            force: force === true ? null : undefined,
            strict: strict === true ? null : undefined,
            serial: serial === true ? null : undefined,
            v: Math.random(),
        },
        { addQueryPrefix: true, encodeValuesOnly: true, strictNullHandling: true }
    );
    const response = await fetch(`${url}${queryString}`, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        credentials: 'same-origin',
        headers: {
            'Content-Type': 'application/json',
            'Accept-Encoding': 'gzip',
        },
        signal: aborter?.signal,
        body: JSON.stringify({
            query,
            variables,
            template: options.template,
        }),
    });
    const responseJson = await response.json();
    // const { statusCode, message } = responseJson;
    return responseJson;
};

export { fetchGraphQl };
