import { Module } from 'vuex';
import { RootState } from '@/store';
import { bindActions, bindMutations } from '@/store/helper';
import Mutations from './mutations';
import Actions from './actions';

export interface PdfState {
    doc: string;
}

export default {
    namespaced: true,
    state: {
        doc: '',
    },
    mutations: bindMutations(Mutations, {
        setDoc: (_) => _.setDoc,
    }),
    actions: bindActions(Actions, {
        prettifyDoc: (_) => _.prettifyDoc,
    }),
} as Module<PdfState, RootState>;
