import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';

Vue.use(VueRouter);

/**
 * Route Configuration.
 *
 * @MetaObject
    {
        icon:         string;  // Icon to show
        text:         string;  // Text in navigation drawer
        title:        string;  // Text in app bar
        disableBack:  boolean; // Set on root Node to disable back button
        hidden: boolean, // Show or hide in navigation drawer
    }
*/
const routes: RouteConfig[] = [
    // Home
    {
        path: '/',
        component: (): Promise<any> => import('@/views/Home.vue'),
        meta: {
            icon: 'mdi-home',
            title: 'MY PHUOC PLANT DASHBOARD',
            text: 'Trang chủ',
        },
    },
    // GraphQL
    {
        name: 'graphql',
        path: '/graphql',
        component: (): Promise<any> => import('@/views/GraphQl.vue'),
        meta: {
            icon: 'mdi-graphql',
            text: 'GraphQL Playground',
        },
    },
    // Liquid
    {
        name: 'liquid',
        path: '/liquid',
        component: (): Promise<any> => import('@/views/Liquid.vue'),
        meta: {
            icon: 'mdi-water',
            text: 'Liquid Playground',
        },
    },
    // Pdf
    {
        name: 'pdf',
        path: '/pdf',
        component: (): Promise<any> => import('@/views/Pdf.vue'),
        meta: {
            icon: 'mdi-file-pdf-box-outline',
            text: 'PdfMake Playground',
        },
    },
    // About
    {
        name: 'about',
        path: '/about',
        component: (): Promise<any> => import('@/views/About.vue'),
        meta: {
            icon: 'mdi-book-information-variant',
            text: 'Thông tin hỗ trợ',
        },
    },
    // Not found
    {
        name: '404',
        path: '*',
        component: (): Promise<any> => import('@/views/404.vue'),
        meta: {
            icon: 'mdi-wrench',
            text: '404',
            title: 'Không tìm thấy trang bạn yêu cầu',
            hidden: true,
        },
    },
    {
        path: '*',
        component: (): Promise<any> => import('@/views/Home.vue'),
        meta: {
            icon: 'mdi-home',
            title: 'MY PHUOC PLANT DASHBOARD',
            text: 'Trang chủ',
        },
    },
];

export default function (filter?: (routeConfig: RouteConfig) => boolean): RouteConfig[] {
    const routerConfig = filter ? routes.filter(filter) : routes;
    return routerConfig;
}
