import { GraphQlState, GraphQlSession, GraphQlConfiguration } from '.';

export default class Mutations {
    constructor(private _state: GraphQlState) {}

    public addSession(_: GraphQlSession): void {
        this._state.sessions.push(_);
    }

    public removeSession(_: GraphQlSession): void {
        const index = this._state.sessions.indexOf(_);
        if (index === -1) return;
        this._state.sessions.splice(index, 1);
    }

    public setActiveSession(_: GraphQlSession): void {
        this._state.activeSession = _;
    }

    public updateActiveSessionInfo(_: GraphQlSession): void {
        const { activeSession } = this._state;
        for (const key of Object.keys(_)) {
            if (activeSession?.hasOwnProperty(key)) {
                activeSession[key] = _[key];
            }
        }
    }

    public updateConfiguration(_: GraphQlConfiguration): void {
        const { configuration } = this._state;
        for (const key of Object.keys(_)) {
            if (configuration?.hasOwnProperty(key)) {
                configuration[key] = _[key];
            }
        }
    }

    public setQuery(_: string): void {
        if (!this._state.activeSession) return;
        this._state.activeSession.query = _;
    }

    public setVariables(_: string): void {
        if (!this._state.activeSession) return;
        this._state.activeSession.variables = _;
    }

    public setVariableToType(_: string): void {
        if (!this._state.activeSession) return;
        this._state.activeSession.variablesToType = _;
    }

    public setTemplate(_: string): void {
        if (!this._state.activeSession) return;
        this._state.activeSession.template = _;
    }

    public setResult(_: string): void {
        if (!this._state.activeSession) return;
        this._state.activeSession.result = _;
    }

    public setIsBusy(_: boolean): void {
        if (!this._state.activeSession) return;
        this._state.activeSession.isBusy = _;
    }
}
