























import Vue from 'vue';
import { mapState, mapMutations } from 'vuex';
import ErrorComponent from '@/components/layout/Error.vue';
import AppBar from '@/components/layout/AppBar.vue';
import { RootState } from './store';
import { SET_ERROR } from './mutation-types';

export default Vue.extend({
    name: 'app',
    components: {
        AppBar,
        ErrorComponent,
    },
    data() {
        return {
            show: false,
        };
    },
    computed: {
        ...mapState<RootState>({ error: 'error' }),
        meta(): { icon: string; title: string; text: string } {
            const { icon, title, text } = this.$route.meta;
            return { icon, title, text };
        },
        error(): undefined | Error {
            return this.$store.state.error;
        },
    },
    methods: {
        ...mapMutations({ setError: SET_ERROR }),
    },
});
