import { ActionContext } from 'vuex';
import { RootState } from '@/store';
import { js_beautify as jsBeautify } from 'js-beautify';
import { PdfState } from '.';

export default class Actions {
    constructor(private _context: ActionContext<PdfState, RootState>) {}

    public prettifyDoc(): void {
        const prettied = jsBeautify(this._context.state.doc, {
            indent_size: 4,
            space_in_empty_paren: true,
            brace_style: 'expand',
            end_with_newline: true,
        });
        this._context.commit('setDoc', prettied);
    }
}

export interface PrettierOptions {
    printWidth: number;
    tabWidth: number;
    useTabs: boolean;
}
