import { v4 } from 'uuid';
import { ActionContext } from 'vuex';
import { RootState } from '@/store';
import { fetchGraphQl } from '@/core/fetch-graphql';
import { js_beautify as jsBeautify } from 'js-beautify';
import * as prettier from 'prettier/standalone';
import * as graphql from 'prettier/parser-graphql';
import { print, parse } from 'graphql';
import mergeAST from '@/views/GraphQl/utils/mergeAst';
import jsesc from 'jsesc';
import { END_POINT } from '../../../main';
import { GraphQlState, GraphQlSession } from '.';

let _aborter: AbortController | null;

export default class Actions {
    constructor(private _context: ActionContext<GraphQlState, RootState>) {}

    public newSession(): void {
        const newSession: GraphQlSession = {
            id: v4(),
            title: 'New tab',
            query: '',
            result: '',
            template: '',
            variables: '',
            variablesToType: '',
            isBusy: false,
            viewCode: false,
        };
        this._context.commit('addSession', newSession);
        this.switchSession(newSession.id);
    }

    public switchSession(sessionId: string): void {
        if (!sessionId) return;
        const selectedSession = this._context.state.sessions.find((x) => x.id === sessionId);
        if (!selectedSession) return;
        this._context.commit('setActiveSession', selectedSession);
    }

    public closeSession(sessionId: string): void {
        if (!sessionId) return;
        const selectedSession = this._context.state.sessions.find((x) => x.id === sessionId);
        if (!selectedSession) return;
        const index = this._context.state.sessions.indexOf(selectedSession);
        if (sessionId !== this._context.state.activeSession?.id) {
            this._context.commit('removeSession', selectedSession);
        } else if (this._context.state.sessions.length > 1) {
            if (index === 0) {
                this._context.commit('setActiveSession', this._context.state.sessions[1]);
            } else {
                this._context.commit('setActiveSession', this._context.state.sessions[index - 1]);
            }
            this._context.commit('removeSession', selectedSession);
        }
    }

    // eslint-disable-next-line class-methods-use-this
    public stopQuery(): void {
        _aborter?.abort();
        console.log('stop');
    }

    public async runQuery(): Promise<void> {
        let result = '';
        try {
            if (!this._context.state.activeSession) return;
            const { query, template } = this._context.state.activeSession;
            if (!query) return;

            const jse = (match, capture): string =>
                `"${jsesc(capture, { quotes: 'double' })
                    .replace(/\n/g, ' ')
                    .replace(/\\n/g, ' ')
                    .replace(/\s\s+/g, ' ')
                    .trim()}"`;
            const queryEscaped = query.replace(/`([^`]*)`/g, jse);

            this._context.commit('setIsBusy', true);
            this._context.commit('setResult', '');
            _aborter = new AbortController();
            const { variables } = this._context.state.activeSession;
            const {
                useCustomEndpoint,
                customEndpoint,
                force,
                paging,
                concurrent,
                serial,
                strict,
            } = this._context.state.configuration;
            let url = END_POINT;
            if (useCustomEndpoint) url = customEndpoint;
            result = await fetchGraphQl(
                queryEscaped,
                {
                    template: template || undefined,
                    aborter: _aborter,
                    url,
                    force,
                    paging,
                    concurrent,
                    strict,
                    serial,
                },
                variables ? JSON.parse(variables) : null
            );
            const prettied = jsBeautify(JSON.stringify(result), {
                indent_size: 4,
                space_in_empty_paren: true,
                brace_style: 'expand',
                end_with_newline: true,
            });
            this._context.commit('setResult', prettied);
        } catch (error) {
            const prettied = jsBeautify(JSON.stringify(error.message), {
                indent_size: 4,
                space_in_empty_paren: true,
                brace_style: 'expand',
                end_with_newline: true,
            });
            this._context.commit('setResult', prettied);
        } finally {
            this._context.commit('updateActiveSessionInfo', {
                viewCode: false,
            });
            this._context.commit('setIsBusy', false);
        }
    }

    public prettifyQuery(): void {
        if (!this._context.state.activeSession) return;
        const currentQuery = this._context.state.activeSession.query;
        const options: PrettierOptions = {
            printWidth: 70,
            tabWidth: 4,
            useTabs: false,
        };
        let formattedQuery = null;
        try {
            formattedQuery = prettier.format(currentQuery, {
                ...options,
                parser: 'graphql',
                plugins: [graphql],
            });
            // eslint-disable-next-line no-empty
        } catch (err) {
        } finally {
            if (formattedQuery !== null && currentQuery !== formattedQuery) {
                this._context.commit('setQuery', formattedQuery);
            }
        }
    }

    public mergeQuery(): void {
        let formattedQuery = null;
        if (!this._context.state.activeSession) return;
        const currentQuery = this._context.state.activeSession.query;
        if (!currentQuery) return;
        try {
            const parsedQuery = parse(currentQuery);
            const mergedQuery = print(mergeAST(parsedQuery));
            const options: PrettierOptions = {
                printWidth: 70,
                tabWidth: 4,
                useTabs: false,
            };
            formattedQuery = prettier.format(mergedQuery, {
                ...options,
                parser: 'graphql',
                plugins: [graphql],
            });
            // eslint-disable-next-line no-empty
        } catch (err) {
        } finally {
            if (formattedQuery !== null && currentQuery !== formattedQuery) {
                this._context.commit('setQuery', formattedQuery);
            }
        }
    }
}

export interface PrettierOptions {
    printWidth: number;
    tabWidth: number;
    useTabs: boolean;
}
